  cursor: pointer;
  position: relative;
  width: 100%;
  height: 100%;
}
.toggle-button__toggle-menu span {
  opacity: 1;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  border-radius: 2px;
  background-color: white;
  position: absolute;
  transform: rotate(0deg);
  transition: 0.4s ease-in-out;
}
