}
.toggle-button__toggle-menu._open span:nth-child(1) {
  transform: scale(1.088) translateY(7px) rotate(135deg);
}
.toggle-button__toggle-menu._open span:nth-child(2) {
  opacity: 0;
  transform: translateX(-60px);
